<template>
    <v-card>
        <v-card-text>
            <v-container>
                <v-flex xs6> 
                   <v-text-field label="DESCRIPCION" v-model="descripcion"></v-text-field>
                </v-flex>
            </v-container>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="info" @click="save()">GUARDAR
                <v-icon dark right>check_circle</v-icon>
            </v-btn>
            <v-btn color="error" @click="$emit('cerrar')">CANCELAR
                <v-icon dark right>check_circle</v-icon>
            </v-btn>
        </v-card-actions>
    </v-card>
</template>
 
<script>
import axios from 'axios';
export default {
    data() {
        return {  
            formTitle: 'NUEVA CATEGORIA',
            descripcion: ''
        }
    },
    methods: {
        save() {
            let me = this;
            axios.post('categoria/save',{
                'descripcion':me.descripcion
            }).then(function(response){
                me.$emit('cerrar');
            }).catch(function(error){
            });
        }
    }
}
</script>