<template>
    <v-layout justify-center>
        <v-flex xs12>
             <v-toolbar dense> 
                    <v-toolbar-title>INFORMACION CATEGORIA</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-btn  v-if="verNuevo==0" color="primary" dark @click="nuevo()">NUEVO<v-icon dark >add</v-icon></v-btn>
             </v-toolbar>
             <v-divider></v-divider>
             <v-card>
                <v-container fluid grid-list-md v-if="verNuevo==0" >
                    <v-layout row wrap>
                        <v-flex xs12>
                            <v-card >
                                    <v-card-title>
                                        LISTA DE CATEGORIA
                                        <v-spacer></v-spacer>
                                        <v-text-field append-icon="mdi-magnify" single-line hide-details></v-text-field>
                                    </v-card-title>
                                    <v-divider></v-divider>
                                    <v-data-table
                                        :headers="cat"
                                        :items="lista"
                                        class="elevation-1">
                                        <template slot="items" slot-scope="props">
                                            <td>
                                                <v-icon small color="red darken-2">delete</v-icon>
                                                <v-icon  small color="primary darken-2" @click="obtenerCategoria(props.item.idcategoria)">edit</v-icon>
                                            </td>
                                            <td>{{props.item.idcategoria}}</td>
                                            <td>{{props.item.descripcion}}</td>
                                            <td>
                                                <div  class="primary darken-2 text-xs-center" v-if="props.item.estado==1">
                                                    <span class="white--text">Activo</span>
                                                </div>
                                                <div  class="error darken-2 text-xs-center" v-else>
                                                    <span class="white--text">Anulado</span>
                                                </div>
                                            </td>
                                        </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3> No hay categorias.</h3>
                                            </span>
                                        </template>
                                    </v-data-table>
                            </v-card>
                         </v-flex>
                    </v-layout>
                </v-container>
                <show   v-if="verNuevo==1" :info="category" @cerrar="cancelar()"/>
                <create v-if="verNuevo==2"  @cerrar="cancelar()"/>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
import axios from 'axios'
import show from './Edit.vue'
import create from './Create.vue'
export default {
    data() {
        return {
            category:'',
            lista: [],
            verNuevo:0,
            data: ['Musica', 'Pelicula', 'Video'],
            cat: [
                { text: 'ACCION', value: '',sortable: false},
                { text: 'ID', value: '',sortable: false},
                { text: 'CATEGORIA', value: '',sortable: false},
                { text: 'ESTADO', value: '',sortable: false},
            ]
        }
    },
    components:{
        show,create
    },
    created(){
        this.getCategoria();
    },
    methods: {
        async getCategoria (){
            try {
                let me= this;
                const response = await axios.get('categoria/listar');
                me.lista = response.data;
            } catch (error) {   
            }
        },
        async obtenerCategoria(idcategoria){
            this.verNuevo=1;
            this.category = idcategoria;
        },
        cancelar () {
            this.verNuevo=0;
            this.getCategoria();
        },
        nuevo() {
             this.verNuevo=2;
        }
    }
}
</script>