<template>
    <v-layout justify-center>
        <v-flex xs12>
             <v-toolbar dense> 
                    <v-toolbar-title>INFORMACION ROLES</v-toolbar-title>
                    <create v-if="ismodal" @cerrar="cancelar"/>
                    <edit   v-if="isedit" :rol="roles"  @cerrar="cancelar"/>
                    <v-spacer></v-spacer>
                    <v-btn  color="primary" dark @click="nuevo()">NUEVO<v-icon dark >add</v-icon></v-btn>
             </v-toolbar>
             <v-divider></v-divider>
             <v-card>
                <v-container fluid grid-list-md  >
                    <v-layout row wrap>
                        <v-flex xs12>
                            <v-card >
                                    <v-card-title>
                                        LISTA DE ROLES
                                        <v-spacer></v-spacer>
                                        <v-text-field append-icon="mdi-magnify" single-line hide-details></v-text-field>
                                    </v-card-title>
                                    <v-divider></v-divider>
                                    <v-data-table
                                        :headers="rol"
                                        :items="lista"
                                        class="elevation-1">
                                        <template slot="items" slot-scope="props">
                                            <td>
                                                <v-icon  small color="primary darken-2" @click="obtenerRol(props.item.idrol)">edit</v-icon>
                                            </td>
                                            <td>{{props.item.idrol}}</td>
                                            <td>{{props.item.descripcion}}</td>
                                        </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3> No hay roles.</h3>
                                            </span>
                                        </template>
                                    </v-data-table>
                            </v-card>
                         </v-flex>
                    </v-layout>
                </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
import axios from 'axios'
import create from './Create.vue'
import edit from './Edit.vue'
export default {
    data() {
        return {
            roles:'',
            ismodal:false,
            isedit:false,
            lista: [],
            rol: [
                    { text: 'ACCION', value: '',sortable: false},
                    { text: 'ID', value: '',sortable: false},
                    { text: 'ROL', value: '',sortable: false},
            ],
        }
    },
    components:{
        create, edit
    },
    created(){
        this.getRol();
    },
    methods: {
        async getRol (){
            try {
                let me= this;
                const response = await axios.get('rol/listar');
                me.lista = response.data;
            } catch (error) {   
            }
        },
        async obtenerRol(idrol){ 
            try {
                let me= this;
                const response = await axios.get('rol/' + idrol);
                me.isedit = true;
                me.roles = response.data;
            } catch (error) {
            }
        },
        cancelar (estado) {
            if(estado =='edit'){
                this.isedit = false;
                this.getRol();
            }else{
                this.ismodal= false;
                this.getRol();  
            }
        },
        nuevo() {
            this.ismodal = true;
        }
    }
}
</script>