<template>
    <v-dialog v-model="modal" max-width="500px" persistent>
        <v-card>
            <v-card-title class="primary" style="color: white;" primary-title>
                <h3>NUEVO ROL</h3>
            </v-card-title>
            <v-card-text> 
                <v-container grid-list-md>
                    <v-layout wrap>
                        <v-flex xs12 sm12 md12>
                            <v-text-field   v-model="descripcion" label="ROL"></v-text-field>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="error" @click="cancelar()">CANCELAR <v-icon dark right>block</v-icon></v-btn>
                <v-btn color="info" @click="save()">GUARDAR<v-icon dark right>check_circle</v-icon></v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
import axios from 'axios';
export default {
    data() {
        return {
            modal: false,
            descripcion: ''
        }
    },
    created(){
        this.modal = true;
    },
    methods: {
        cancelar(){
            this.modal = false;
            this.$emit('cerrar');
        },
        save (){
            let me = this;
            axios.post('rol/save',{
                'descripcion':me.descripcion
            }).then(function(response){
                me.modal = false;
                me.$emit('cerrar');
            }).catch(function(error){
            });
        }
    }
}
</script>