<template>
    <v-card>
        <v-card-text>
            <v-container>
                <v-flex xs6> 
                   <v-text-field v-model="category.descripcion" label="DESCRIPCION" ></v-text-field>
                </v-flex>
            </v-container>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="info" @click="updated()">GUARDAR
                <v-icon dark right>check_circle</v-icon>
            </v-btn>
            <v-btn color="error" @click="$emit('cerrar')">CANCELAR
                <v-icon dark right>check_circle</v-icon>
            </v-btn>
        </v-card-actions>
    </v-card>
</template>
 
<script>
import axios from 'axios';
export default {
    props: { 
        info: {
          type: Number
        }
    },
    data() {
        return {  
            formTitle: 'ACTUALIZAR CATEGORIA' ,
            category:''
        }
    },
    created(){
       this.cat();
    },
    methods: {
        updated() {
            let me = this;
            axios.put('categoria/update',{
                'idcategoria': me.category.idcategoria,
                'descripcion':me.category.descripcion
            }).then(function(response){
                me.$emit('cerrar');
            }).catch(function(error){
            });
        },
        async cat() {
            try {
                let me= this;
                const response = await axios.get('categoria/' + this.info);
                me.category = response.data;
            } catch (error) {
            }
        }
    }
}
</script>